<template>
	<div class="nav">
		<div class="icons">
			<div class="icon">
				<div class="icon-img">
					<img class="icon-img-content" src="http://pic5.40017.cn/01/000/5f/30/rBANC1qozNqAb3vRAAAD_Qg-dt4234.png">
				</div>
				<p class="icon-desc">火车票</p>
			</div>
			<div class="icon">
				<div class="icon-img">
					<img class="icon-img-content" src="http://pic5.40017.cn/02/001/5f/ab/rBLkCFqozNqAJqPYAAAFlYq1v7A413.png">
				</div>
				<p class="icon-desc">机票</p>
			</div>
			<div class="icon">
				<div class="icon-img">
					<img class="icon-img-content" src="http://pic5.40017.cn/01/000/5f/a5/rBLkBVqozNqAGbVbAAADpuwJ0xY082.png">
				</div>
				<p class="icon-desc">酒店住宿</p>
			</div>
			<div class="icon">
				<div class="icon-img">
					<img class="icon-img-content" src="http://pic5.40017.cn/02/001/5f/ab/rBLkCFqozNqAF4UUAAAFH-2jq70957.png">
				</div>
				<p class="icon-desc">景点门票</p>
			</div>
		</div>
		
		<div class="subNav">
			<swiper :options="swiperOption">
				<swiper-slide v-for="(page,index) of pages" :key="index">
					<div 
					class="clearfix" 
					v-for="item of page" 
					:key="item.id">
						<div class="clearfix-img">
							<img class="clearfix-img-content" :src="item.imgUrl"/>
						</div>
						<p class="clearfix-desc">{{item.desc}}</p>
					</div>
				</swiper-slide>
			</swiper>
		</div>
	</div>
</template>

<script>
	export default{
		name:'HomeIcons',
		props:{
			list:Array
		},
		data(){
			return{
				swiperOption: {
					autoplay:0,
				}
			}
		},
	computed: {
	 pages () {
			const pages =  []
			this.list.forEach((item,index) => {
				const page = Math.floor(index / 8)
				if(!pages[page]){
					pages[page]=[]
				}
				pages[page].push(item)
				})
				return pages
			}
		} 
	}
</script>

<style lang="stylus" scoped>
	@import '../../assets/style/varibles.styl'
	@import '../../assets/style/mixins.styl'
	.nav
		padding: .53rem .4rem 0 .4rem
		background: #fff
		.icons
			overflow:hidden
			height:0
			padding-bottom:30%
			.icon
				position:relative
				overflow:hidden
				float:left
				width:25%
				height:0
				padding-bottom:25%
				.icon-img
					position:absolute
					top:0
					left:0
					right:0
					bottom:.64rem
					box-sizing: border-box
					padding:.1rem
					.icon-img-content
						display:block
						margin:0 auto
						height:100%
				.icon-desc
					position:absolute
					left:0
					right:0
					bottom:0
					height:.44rem
					line-height:.24rem
					text-align:center
					color:$darkTextColor
		.subNav >>> .swiper-container
			padding-top:.24rem
			height:0
			border-top:1px solid
			padding-bottom:50%
		.subNav
			margin-top:.1rem
			.clearfix
				position:relative
				overflow:hidden
				float:left
				width:25%
				height:0
				padding-bottom:23%
				.clearfix-img
					position:absolute
					top:0
					left:0
					right:0
					bottom:.84rem
					box-sizing: border-box
					padding:.1rem
					.clearfix-img-content
						display:block
						margin:0 auto
						height:100%
				.clearfix-desc
					position:absolute
					left:0
					right:0
					bottom:0
					height:.64rem
					line-height:.24rem
					text-align:center
					color:$darkTextColor
					ellipsis()
</style>