<template>
  <div class="home">
		<home-header></home-header>
		<home-swiper :list="swiperList"></home-swiper>
		<home-icons :list="clearfixList"></home-icons>
		<home-recommend :list="recommendList"></home-recommend>
		<home-weekend :list="weekendList"></home-weekend>
  </div>
</template>

<script>
import HomeHeader from "@/components/Home/Homeheader.vue";
import HomeSwiper from "@/components/Home/Swiper.vue";
import HomeIcons from "@/components/Home/icons.vue";
import HomeRecommend from "@/components/Home/Recommend.vue";
import HomeWeekend from "@/components/Home/Weekend.vue"
import axios from "axios"
import { mapState } from "vuex"
export default {
  name: "home",
  components: {
    HomeHeader,
		HomeSwiper,
		HomeIcons,
		HomeRecommend,
		HomeWeekend
  },
	data(){
		return{
			lastCity:'',
			swiperList:[],
			clearfixList:[],
			recommendList:[],
			weekendList:[]
		}
	},
	computed:{
		...mapState(['city'])
	},
	methods:{
		getHomeInfo(){
			axios.get('api/index.json?city='+ this.city)
			.then(this.getHomeInfoSucc)
		},
		getHomeInfoSucc(res){
			console.log(res)
			res = res.data
			if (res.ret && res.data){
				const data = res.data
				this.swiperList = data.swiperList
				this.clearfixList = data.clearfixList
				this.recommendList = data.recommendList
				this.weekendList = data.weekendList
			}
		}
	},
	mounted(){
		this.lastCity = this.city
		this.getHomeInfo()
	},
	activated () {
		if (this.lastCity !== this.city) {
			this.lastCity = this.city
			this.getHomeInfo()
		}
	}
}
</script>